import React, { useState } from "react";
import "./StepTwo.css";
import { Radio } from "antd";
import _ from "lodash";
import { useHistory } from "react-router-dom";
import axios from "axios";
import ReactMarkdown from "react-markdown";
import ReactGA from 'react-ga';
ReactGA.initialize('UA-161437152-1');
ReactGA.pageview(window.location.pathname + window.location.search);

function StepTwo(props) {
  const me = props.location.state.detail.me;
  const ta = props.location.state.detail.ta;
  const init = () => {
    console.log("init called");
    let tempArr = [me, ta];
    return _.shuffle(tempArr);
  };
  const [value, setValue] = useState();
  const [answer, setAnswer] = useState("-");
  const [solution, setSolution] = useState(init());

  const history = useHistory();

  const onChange = (val, e) => {
    // 여기서 정/오답을 판별하면 되겠네요
    setValue(e.target.value);
    if (e.target.value == val) {
      setAnswer("o");
    } else {
      setAnswer("x");
    }
  };

  const handleClick = () => {
    axios
      .get(
        `https://his8t8n927.execute-api.ap-northeast-2.amazonaws.com/dev/quiz?my=${me.birth}&ta=${ta.birth}&answer=${answer}`
      )
      .then(response => {
        const result = { answer: answer, detail: props.location.state.detail };
        history.push({
          pathname: "/StepThree",
          state: { data: result }
        });
      });
  };

  return (
    <div className="container">
      <div className="container">
        <br />
        <br />
        <div className="title">step 2</div>
        <br />
        <br />
        <div className="title-name">
          성격의 결과를 읽어보고
          <br />
          누구의 것인지 맞춰보세요!
        </div>
        <br />
        <div style={{border: "red 1px dashed", padding: "10px"}}>
          <ReactMarkdown source={solution[0].explain} />
        </div>
        <br />
        <br />
        <div>
          위의 내용은 [
          <Radio.Group
            onChange={e => onChange(solution[0].day, e)}
            value={value}
          >
            <Radio value={me.day}>나</Radio>
            <Radio value={ta.day}>상대방</Radio>
          </Radio.Group>]의 것입니다.
        </div>
        <br />
        <br />
        <div style={{border: "blue 1px dashed", padding: "10px"}}>
          <ReactMarkdown source={solution[1].explain} />
        </div>
        <br />
        <div>
          위의 내용은 [
          <Radio.Group
            onChange={e => onChange(solution[1].day, e)}
            value={value}
          >
            <Radio value={ta.day}>나</Radio>
            <Radio value={me.day}>상대방</Radio>
          </Radio.Group>]의 것입니다.
        </div>
        <br />
        <br />
        <br />
        <div>
          <button className="confirm" onClick={handleClick}>
            정답 제출 👉
          </button>
        </div>
      </div>
    </div>
  );
}

export default StepTwo;
